<template>
  <div
    :id="galleryID"
    :class="className"
    class="w-100 overflow-hidden clear-float img-viewer"
  >
    <a
      v-for="(image, key) in imagesData"
      :key="key"
      :href="getImg(image.largeURL)"
      target="_blank"
      rel="noreferrer"
      :data-pswp-width="image.width"
      :data-pswp-height="image.height"
      :class="key == imagesData.length - 1 ? 'mr-0' : ''"
      class="d-block quartor card p-4 float-left"
    >
      <img class="w-100" :src="getImg(image.thumbnailURL)" alt="" />
    </a>
  </div>
</template>
  
  <script>
import PhotoSwipeLightbox from "photoswipe/lightbox";
import "photoswipe/style.css";

export default {
  name: "SimpleGallery",
  props: {
    galleryID: String,
    images: Array,
    className: String,
  },
  computed: {
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
  },
  setup(props) {
    return {
      imagesData: props.images,
    };
  },
  mounted() {
    if (!this.lightbox) {
      this.lightbox = new PhotoSwipeLightbox({
        gallery: "#" + this.$props.galleryID,
        children: "a",
        pswpModule: () => import("photoswipe"),
      });
      this.lightbox.init();
    }
  },
  unmounted() {
    if (this.lightbox) {
      this.lightbox.destroy();
      this.lightbox = null;
    }
  },
  methods: {},
};
</script>
  